<template>
  <!--  -->
  <y-form-model-item label="">
    <a-row>
      <a-col :span="12"><y-checkbox v-model="optionData.hidden">  </y-checkbox></a-col>
      <a-col :span="12" v-if="disabledCheck"><y-checkbox v-model="optionData.disabled">  </y-checkbox></a-col>
      <a-col :span="12" v-if="importData"><y-checkbox v-model="optionData.importData">  </y-checkbox></a-col>
      <a-col :span="12" v-if="exportData"><y-checkbox v-model="optionData.exportData">  </y-checkbox></a-col>
      <a-col :span="12" v-if="lineFeed"
        ><y-tooltip>
          <template slot="title">
            <span></span>
          </template>
          <y-checkbox v-model="optionData.lineFeed">
            <span></span>
          </y-checkbox>
        </y-tooltip></a-col
      >
      <a-col :span="12"
        ><y-tooltip>
          <template slot="title">
            <span></span>
          </template>
          <y-checkbox v-model="optionData.logicRulesIsAnd">
            <span></span>
          </y-checkbox>
        </y-tooltip></a-col
      >
      <a-col :span="24" v-if="shouChildLabel"><y-checkbox v-model="optionData.showChildComponentLabel">  </y-checkbox></a-col>
    </a-row>
  </y-form-model-item>
</template>
<script>
import mixins from './mixins';
export default {
  name: 'OperationalAttribute',
  mixins: [mixins],
  props: {
    shouChildLabel: {
      type: Boolean,
      default: false,
    },
    // 
    disabledCheck: {
      type: Boolean,
      default: true,
    },
    // 
    lineFeed: {
      type: Boolean,
      default: false,
    },
    // 
    importData: {
      type: Boolean,
      default: false,
    },
    // 
    exportData: {
      type: Boolean,
      default: false,
    },
  },
};
</script>
