<template>
  <y-form-model class="select-filed" ref="ruleForm" :model="optionData">
    <!--  -->
    <SetTitle v-model="optionData" />

    <!--  -->
    <SetModel v-model="optionData" />

    <!--  -->
    <TipsConfig v-model="optionData" type="fileupload" />

    <y-form-model-item label="">
      <y-input-number class="limit-input" :min="0" autoWidth v-model="optionData.limitSize" placeholder="" />
      <span>MB</span>
    </y-form-model-item>
    <y-form-model-item label="">
      <y-input-number class="limit-input" :min="0" autoWidth v-model="optionData.limit" placeholder="" />
      <span></span>
    </y-form-model-item>
    <y-form-model-item label="">
      <y-input style="width: 100%" autoWidth v-model="optionData.accept" placeholder=".png,.jpg,.jpeg,.doc" />
    </y-form-model-item>
    <y-form-model-item label="">
      <y-select autoWidth v-model="optionData.fileType" :options="fileTypeOptions"></y-select>
    </y-form-model-item>
    <y-form-model-item label="">
      <y-radio-group v-model="optionData.required" :radio="false" bodered>
        <y-radio-content :value="true">  </y-radio-content>
        <y-radio-content :value="false">  </y-radio-content>
      </y-radio-group>
    </y-form-model-item>
    <!--  -->
    <!-- <SetLogicForm v-model="optionData" /> -->

    <!--  -->
    <!-- <AddButton v-model="optionData" /> -->

    <!--  -->
    <SetItemWidth v-model="optionData" />

    <!--  -->
    <!-- <SetWidth v-model="optionData" /> -->

    <!--  -->
    <OperationalAttribute v-model="optionData" />
  </y-form-model>
</template>
<script>
import TipsConfig from './common/tipsConfig';
// import AddButton from './common/addButton';
import SetItemWidth from './common/setItemWidth';
// import SetWidth from './common/setWidth';
import SetTitle from './common/setTitle';
import SetModel from './common/setModel';
// import SetLogicForm from './common/setLogicForm';
import OperationalAttribute from './common/operationalAttribute';

export default {
  name: 'InputFiled',
  components: {
    TipsConfig,
    // AddButton,
    SetItemWidth,
    SetTitle,
    SetModel,
    // SetLogicForm,
    // SetWidth,
    OperationalAttribute,
  },
  model: {
    prop: 'data',
    event: 'update-data',
  },
  props: {
    data: {
      type: Object,
      default: function() {
        return {};
      },
    },
  },
  computed: {
    optionData: {
      get({ data }) {
        return data;
      },
      set(val) {
        this.$emit('update-data', val);
      },
    },
  },
  data() {
    return {
      fileTypeOptions: [
        {
          label: '',
          value: '1',
        },
        {
          label: '',
          value: '2',
        },
        {
          label: '',
          value: '3',
        },
        {
          label: '',
          value: '4',
        },
        {
          label: '',
          value: '5',
        },
      ],
    };
  },
};
</script>
<style lang="less">
.select-filed {
  .limit-input {
    width: 85%;
    margin-right: 4px;
  }
}
</style>
