<template>
  <!--  -->
  <y-form-model-item label="">
    <y-select autoWidth v-model="optionData.format" :options="dateFormatOptions"></y-select>
  </y-form-model-item>
</template>
<script>
import mixins from './mixins';
export default {
  name: 'DateFormat',
  mixins: [mixins],
  data() {
    return {
      dateFormatOptions: [
        {
          label: '',
          value: 'YYYY',
        },
        {
          label: '-',
          value: 'YYYY-MM',
        },
        {
          label: '--',
          value: 'YYYY-MM-DD',
        },
        {
          label: '-- :',
          value: 'YYYY-MM-DD HH:mm',
        },
        {
          label: '-- ::',
          value: 'YYYY-MM-DD HH:mm:ss',
        },
      ],
    };
  },
};
</script>
