<template>
  <y-form-model class="select-filed" ref="ruleForm" :model="optionData">
    <!--  -->
    <y-form-model-item label="">
      <y-input autoWidth v-model="optionData.name2" placeholder="" />
    </y-form-model-item>
    <!--  -->
    <SetTitle v-model="optionData" label="" />

    <!--  -->
    <!-- <y-form-model-item label="">
      <y-input autoWidth v-model="optionData.model" placeholder="" />
    </y-form-model-item> -->

    <!--  -->
    <!-- <y-form-model-item label="">
      <y-input autoWidth v-model="optionData.model2" placeholder="" />
    </y-form-model-item> -->

    <!--  -->
    <TipsConfig v-model="optionData" />
    <!--  -->
    <!-- <y-form-model-item label="">
      <draggable
        v-bind="{
          group: { name: 'inputOptionGroup' },
        }"
        :sort="true"
        draggable=".input-option"
        v-model="optionData.options"
      >
        <div class="input-option" v-for="(item, index) in optionData.options" :key="index">
          <y-input autoWidth placeholder="" v-model="item.value" />
          <a-icon type="delete" @click="deleteCustomOptions(index)" />
          <y-svg-icon icon-class="dragLine" class-name="line" />
        </div>
      </draggable>
      <y-button style="width: 100%" @click="addCustomOptions" icon="plus" type="dashed"></y-button>
    </y-form-model-item> -->

    <!--  -->
    <!-- <y-form-model-item label="">
      <y-input-number style="width: 100%" :min="0" autoWidth v-model="optionData.maxLength" placeholder="" />
    </y-form-model-item> -->
    <LimitedNumber v-model="optionData" />

    <!--  -->
    <!-- <y-form-model-item label="">
      <y-input style="width: 100%" autoWidth v-model="optionData.defaultValue" placeholder="" />
    </y-form-model-item> -->

    <y-form-model-item label="">
      <y-radio-group v-model="optionData.required" :radio="false" bodered>
        <y-radio-content :value="true">  </y-radio-content>
        <y-radio-content :value="false">  </y-radio-content>
      </y-radio-group>
    </y-form-model-item>

    <!-- <y-form-model-item label="">
      <y-radio-group v-model="optionData.isCheck" :radio="false" bodered>
        <y-radio-content :value="true"> </y-radio-content>
        <y-radio-content :value="false">  </y-radio-content>
      </y-radio-group>
    </y-form-model-item> -->
    <!--  -->
    <!-- <SetLogicForm v-model="optionData" /> -->

    <!--  -->
    <!-- <SetItemWidth v-model="optionData" /> -->

    <!--  -->
    <SetWidth v-model="optionData" />

    <!--  -->
    <OperationalAttribute lineFeed v-model="optionData" />
  </y-form-model>
</template>
<script>
// import draggable from 'vuedraggable';
import TipsConfig from './common/tipsConfig';
// import SetItemWidth from './common/setItemWidth';
import SetTitle from './common/setTitle';
import LimitedNumber from './common/limitedNumber';
// import SetLogicForm from './common/setLogicForm';
import OperationalAttribute from './common/operationalAttribute';
import SetWidth from './common/setWidth';
export default {
  name: 'ClassicOpinionFiled',
  components: {
    // draggable,
    TipsConfig,
    // SetItemWidth,
    SetTitle,
    LimitedNumber,
    // SetLogicForm,
    OperationalAttribute,
    SetWidth,
  },
  model: {
    prop: 'data',
    event: 'update-data',
  },
  props: {
    data: {
      type: Object,
      default: function() {
        return {};
      },
    },
  },
  computed: {
    optionData: {
      get({ data }) {
        return data;
      },
      set(val) {
        this.$emit('update-data', val);
      },
    },
  },
  methods: {
    // 
    addCustomOptions() {
      const options = this.optionData.options;
      options.push({ value: '' });
      this.$set(this.optionData, 'options', options);
    },
    deleteCustomOptions(index) {
      let options = this.optionData.options;
      options = options.filter((i, idx) => {
        return idx !== index;
      });
      this.$set(this.optionData, 'options', options);
    },
  },
};
</script>
