<template>
  <y-form-model class="select-filed" ref="ruleForm" :model="optionData">
    <!--  -->
    <SetTitle label="" v-model="optionData" />

    <!--  -->
    <SetModel label="" v-model="optionData" />

    <!--  -->
    <TipsConfig v-model="optionData" :strongTips="false" />

    <!--  -->
    <DataSource v-model="optionData" />

    <!--  -->
    <SetAvailableRange v-model="optionData" />

    <!--  -->
    <SetDefaultValue v-model="optionData" />

    <y-form-model-item label="">
      <y-radio-group v-model="optionData.required" :radio="false" bodered>
        <y-radio-content :value="true">  </y-radio-content>
        <y-radio-content :value="false">  </y-radio-content>
      </y-radio-group>
    </y-form-model-item>

    <y-form-model-item label="">
      <y-radio-group v-model="optionData.filterable" :radio="false" bodered>
        <y-radio-content :value="true">  </y-radio-content>
        <y-radio-content :value="false">  </y-radio-content>
      </y-radio-group>
    </y-form-model-item>

    <y-form-model-item label="">
      <y-radio-group v-model="optionData.multiple" :radio="false" bodered>
        <y-radio-content :value="true">  </y-radio-content>
        <y-radio-content :value="false">  </y-radio-content>
      </y-radio-group>
    </y-form-model-item>
    <!--  -->
    <!-- <SetLogicForm v-model="optionData" /> -->

    <!--  -->
    <!-- <AddButton v-model="optionData" /> -->

    <!--  -->
    <!-- <SetItemWidth v-model="optionData" /> -->

    <!--  -->
    <SetWidth v-model="optionData" />

    <!--  -->
    <OperationalAttribute v-model="optionData" />
  </y-form-model>
</template>
<script>
import TipsConfig from './common/tipsConfig';
import DataSource from './common/dataSource';
import SetAvailableRange from './common/setAvailableRange';
import SetDefaultValue from './common/setDefaultValue';
// import SetLogicForm from './common/setLogicForm';
// import AddButton from './common/addButton';
// import SetItemWidth from './common/setItemWidth';
import SetTitle from './common/setTitle';
import SetModel from './common/setModel';
import OperationalAttribute from './common/operationalAttribute';
import SetWidth from './common/setWidth';
export default {
  name: 'SelectFiled',
  components: {
    TipsConfig,
    DataSource,
    SetAvailableRange,
    SetDefaultValue,
    // SetLogicForm,
    // AddButton,
    // SetItemWidth,
    SetTitle,
    SetModel,
    OperationalAttribute,
    SetWidth,
  },
  model: {
    prop: 'data',
    event: 'update-data',
  },
  props: {
    data: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  computed: {
    optionData: {
      get({ data }) {
        return data;
      },
      set(val) {
        this.$emit('update-data', val);
      },
    },
  },
};
</script>
