<template>
  <y-form-model class="select-filed" ref="ruleForm" :model="optionData">
    <!--  -->
    <SetTitle v-model="optionData" />

    <!--  -->
    <SetModel v-model="optionData" />

    <!--  -->
    <TipsConfig v-model="optionData" />

    <y-form-model-item label="" class="suffix-input">
      <y-input autoWidth v-model="optionData.formatter" placeholder="" />
    </y-form-model-item>

    <!--  -->
    <SetValidate v-model="optionData" />

    <y-form-model-item label="">
      <y-input-number style="width: 100%" autoWidth v-model="optionData.step" placeholder="" />
    </y-form-model-item>

    <y-form-model-item label="">
      <y-input-number style="width: 47%" autoWidth v-model="optionData.min" placeholder="" />
      -
      <y-input-number style="width: 47%" autoWidth v-model="optionData.max" placeholder="" />
    </y-form-model-item>

    <!--   -->
    <y-form-model-item>
      <span slot="label"
        ><y-tooltip title="" style="margin-left: 8px">
          <a-icon type="info-circle" /> </y-tooltip
      ></span>
      <div class="flex-item">
        <y-checkbox v-model="optionData.sumTotalCheck"></y-checkbox>
        <y-input-number class="checkbox-input" autoWidth v-model="optionData.sumTotal" placeholder="" />
      </div>
    </y-form-model-item>

    <y-form-model-item label="">
      <y-input-number style="width: 100%" autoWidth v-model="optionData.defaultValue" placeholder="" />
    </y-form-model-item>

    <y-form-model-item label="">
      <y-radio-group v-model="optionData.required" :radio="false" bodered>
        <y-radio-content :value="true">  </y-radio-content>
        <y-radio-content :value="false">  </y-radio-content>
      </y-radio-group>
    </y-form-model-item>
    <!--  -->
    <!-- <SetLogicForm v-model="optionData" /> -->

    <!--  -->
    <AddButton v-model="optionData" />

    <!--  -->
    <SetItemWidth v-model="optionData" />

    <!--  -->
    <SetWidth v-model="optionData" />

    <!--  -->
    <OperationalAttribute v-model="optionData" />

    <!--  -->
    <SetBilling v-model="optionData" />
  </y-form-model>
</template>
<script>
import TipsConfig from './common/tipsConfig';
import AddButton from './common/addButton';
import SetTitle from './common/setTitle';
import SetModel from './common/setModel';
// import SetLogicForm from './common/setLogicForm';
import SetItemWidth from './common/setItemWidth';
import OperationalAttribute from './common/operationalAttribute';
import SetWidth from './common/setWidth';
import SetValidate from './common/setValidate';
import SetBilling from './common/setBilling';
export default {
  name: 'SelectFiled',
  components: {
    TipsConfig,
    AddButton,
    SetTitle,
    SetModel,
    // SetLogicForm,
    SetItemWidth,
    OperationalAttribute,
    SetWidth,
    SetValidate,
    SetBilling,
  },
  model: {
    prop: 'data',
    event: 'update-data',
  },
  props: {
    data: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  computed: {
    optionData: {
      get({ data }) {
        return data;
      },
      set(val) {
        this.$emit('update-data', val);
      },
    },
  },
};
</script>
