<template>
  <!--  -->
  <y-form-model-item label="">
    <div class="flex-item" v-if="placeholder && isSpaceTip">
      <y-checkbox v-model="optionData[placeholderCheck]"></y-checkbox>
      <y-input
        class="checkbox-input"
        :disabled="!optionData[placeholderCheck]"
        autoWidth
        v-model="optionData[placeholders]"
        placeholder=""
      />
    </div>
    <div class="flex-item" v-if="lightTips">
      <y-checkbox v-model="optionData[lightTipsCheck]"></y-checkbox>
      <y-input
        :disabled="!optionData[lightTipsCheck]"
        class="checkbox-input"
        autoWidth
        v-model="optionData[lightTipsContent]"
        placeholder=""
      />
    </div>
    <div class="flex-item" v-if="strongTips">
      <y-checkbox v-model="optionData[strongTipsCheck]"></y-checkbox>
      <y-input
        :disabled="!optionData[strongTipsCheck]"
        class="checkbox-input"
        autoWidth
        v-model="optionData[strongTipsContent]"
        placeholder=""
      />
    </div>
  </y-form-model-item>
</template>
<script>
import mixins from './mixins';
export default {
  name: 'TipsConfig',
  props: {
    placeholder: {
      type: Boolean,
      default: true,
    },
    lightTips: {
      type: Boolean,
      default: true,
    },
    strongTips: {
      type: Boolean,
      default: true,
    },
    placeholderCheck: {
      type: String,
      default: 'placeholderCheck',
    },
    lightTipsCheck: {
      type: String,
      default: 'lightTipsCheck',
    },
    strongTipsCheck: {
      type: String,
      default: 'strongTipsCheck',
    },
    placeholders: {
      type: String,
      default: 'placeholder',
    },
    lightTipsContent: {
      type: String,
      default: 'lightTipsContent',
    },
    strongTipsContent: {
      type: String,
      default: 'strongTipsContent',
    },
    //
    type: {
      type: String,
      default: 'input',
    },
  },
  mixins: [mixins],
  computed: {
    //fileupload  workOrderBackfill 
    isSpaceTip({ type }) {
      return !['fileupload', 'workOrderBackfill'].includes(type);
    },
  },
};
</script>
